// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import { Button, Form ,Toast, Field, RadioGroup, Radio, Picker, Popup} from "vant";
import VueResource from 'vue-resource';


Vue.use(VueResource);
Vue.use(Button)
Vue.use(Form)
Vue.use(Toast)
Vue.use(Field)
Vue.use(Radio)
Vue.use(RadioGroup)
Vue.use(Picker)
Vue.use(Popup)
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



