const formatAmount = value => {
    if (!value) {
      return '0.00'
    }
    value = value.toString();
    if (value.indexOf('.') > 0){
      var amount = value.split(".");
      if (amount[1].length === 0) {
        return value + '00';
      }else if (amount[1].length === 1) {
        return value + '0';
      }else if (amount[1].length === 2) {
        return value;
      }else {
        return amount[0] + '.' + amount[1].substring(0,2);
      }
    } else {
      return value + '.00';
    }
  }
  
  const amountValidate = amount => {
    var reg = /^[0-9]+([.]{1}[0-9]{1,2})?$/;
    return amount.replace(" ", "") != '' && reg.test(amount);
  }

  module.exports = {
    formatAmount: formatAmount,
    amountValidate: amountValidate
  }


