<template>
  <div class="home">
    <van-form validate-first @submit="onSubmit" class="home-form" @failed="onFailed">
      <van-field
        type="number"
        v-if="candyType==='0'"
        label="单个金额"
        placeholder="输入金额"
        v-model="generalAmount"
        name="generalAmount"
        @input="setGeneralAmount"
        :rules="[{ pattern: candyAmountPattern ,message: '请输入正确的金额，小数保留两位'}, { required:true ,message: '请输入有效金额'}]"
      />
      <van-field
        type="number"
        v-else-if="candyType==='1'"
        label="总金额"
        placeholder="输入金额"
        v-model="luckyAmount"
        name="luckyAmount"
        @input="setLuckyAmount"
        :rules="[{ pattern: candyAmountPattern ,message: '请输入正确的金额，小数保留两位'}, { required:true ,message: '请输入有效金额'}, ]"
      />
      <van-field
        readonly
        clickable
        name="picker"
        :value="coinName"
        label="选择币种"
        placeholder="点击选择币种"
        @click="showPicker = true"
      />
      <van-popup v-model="showPicker" position="bottom" style="width:">
        <van-picker
          show-toolbar
          default-index="0"
          title="币种类型"
          :columns="columns"
          @confirm="onConfirm"
          @cancel="showPicker = false"
        />
      </van-popup>
      <div style="margin-top: 16px;">
        <van-field label="红包类型" name="candyType">
          <template #input>
            <van-radio-group v-model="candyType" direction="horizontal">
              <van-radio name="0">普通红包</van-radio>
              <van-radio name="1">拼手气红包</van-radio>
            </van-radio-group>
          </template>
        </van-field>
      </div>
      <div style="margin-top: 16px;">
        <van-field
          name="candyNumber"
          type="number"
          label="红包个数"
          placeholder
          v-model="candyNumber"
          :rules="[{ pattern: candyNumberPattern ,message: '红包个数填写有误'}]"
        />
      </div>
      <div style="margin-top: 16px;">
        <van-field
          name="remark"
          type="textarea"
          label="红包备注"
          placeholder
          v-model="candyRemark"
          :rules="[{ required:true ,message: '备注不可为空'}]"
        />
      </div>
      <h1 class="amount-label">{{candyAmount}} {{coinName}}</h1>
      <div style="margin: 16px;">
        <van-button round block type="info" size="large" color="#d82b35" native-type="submit">封红包</van-button>
      </div>
    </van-form>
    <h6>24小时内未领取，红包数额将退回</h6>
  </div>
</template>

<script>
import donut from "../js/donut";
import { saveCandyInfoUrl, coinsUrl } from "../js/hosts";
import { amountValidate, formatAmount } from "../js/utils";
export default {
  data() {
    return {
      columns: [],
      candyType: "0",
      luckyAmount: 0,
      generalAmount: 0,
      candyAmount: "0.00",
      showPicker: false,
      candyNumber: 1,
      candyRemark: "给您发了一封红包，快来领取吧!",
      coinName: "SWTC",
      candyNumberPattern: /^[1-9]\d*$/,
      candyAmountPattern: /^[0-9]+([.]{1}[0-9]{1,2})?$/,
      issuer: ""
    };
  },
  mounted() {
    this.getCoins()
  },
  methods: {
    getCoins() {
      let json = require('../assets/coins');
      this.columns = json;
    },
    onFailed(errorInfo) {
      console.log("failed", errorInfo);
    },
    onSubmit(value) {
      var tx = {
        to: "jBvrdYc6G437hipoCiEpTwrWSRBS2ahXN6",
        currency: this.coinName,
        value: this.candyAmount,
        memo:this.candyRemark,
        number:this.candyNumber,
        type:this.candyType,
        gas: "0.00001",
        issuer: this.issuer
      };
      console.log(tx);
      donut.transfer(tx).then(res => {
        showMsgElement(JSON.stringify(res));
      });
    },
    onConfirm(value, index) {
      console.log(value)
      console.log(`当前值：${value}, 当前索引：${index}`);
      this.showPicker = false;
      this.coinName = value.text;
      this.issuer = value.issuer;
    },
    onChange(picker, value, index) {
      console.log(`当前值：${value}, 当前索引：${index}`);
    },
    setLuckyAmount(value) {
      console.log(value)
      this.luckyAmount = value;
      if(this.candyType === "1") {
        this.candyAmount = formatAmount(this.luckyAmount);
      }
    },
    setGeneralAmount(value) {
      console.log(value)
      this.generalAmount = value;
      if(this.candyType === "0") {
        this.candyAmount = formatAmount(this.generalAmount);
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.home {
  margin: 0;
  padding: 30px 0px;
}
.amount-label {
  text-overflow: ellipsis;
  overflow: hidden;
}
.van-radio__icon--checked .van-icon {
  color: #fff;
  background-color: #d82b35 !important;
  border-color: #d82b35 !important;
}
</style>



